import React from 'react';
import { Link } from 'react-router-dom';
import fotoPaquetes from '../images/paquetes.avif';
import imgCultural from '../images/paquetecultural.jpg';
import imgNocturno from '../images/paquetenocturno.png';
import imgLujo from '../images/paquetelujo.jpg';
import '../css/packages.css';

const packages = [
    {
        title: "Turismo Cultural",
        description: "Descubre el misterio y la grandeza de la Catedral de Lima bajo la magia de la noche. Recorre este majestuoso templo iluminado, donde cada rincn guarda siglos de historia y arte colonial.",
        route: "/tourism/cultural",
        image: imgCultural,
        alt: "Imagen de Turismo Cultural"
    },
    {
        title: "Turismo Nocturno",
        description: "Djate maravillar por la magia de la noche en el impresionante Parque de las Aguas de Lima. Un espectculo de fuentes, luces y colores te espera en el circuito acutico ms grande del Per.",
        route: "/tourism/nocturnal",
        image: imgNocturno,
        alt: "Imagen de Turismo Nocturno"
    },
    {
        title: "Turismo de Lujo",
        description: "Vive una noche inolvidable en Larcomar, el icnico centro de entretenimiento y lujo frente al majestuoso ocano Pacfico. Disfruta de una cena gourmet con vista al mar.",
        route: "/tourism/luxury",
        image: imgLujo,
        alt: "Imagen de Turismo de Lujo"
    }
];

const Packages = () => {
    return (
        <div className='page-packages'>
            <div className="intro-packages">
                <h2><em>Paquetes Tursticos con Descuentos</em></h2>
                <p>
                    Convierte tus sueos en realidad con nuestros paquetes tursticos personalizados,
                    donde cada detalle est pensado para que vivas momentos nicos en los destinos ms
                    espectaculares.
                </p>
                <img className="photo" src={fotoPaquetes} alt="imagen para paquetes" />
            </div>

            <section className="cards-container">
                {packages.map((pack, index) => (
                    <article className="card" key={index}>
                        <div className="package-content">
                            <h2><em>{pack.title}</em></h2>
                            <p>{pack.description}</p>
                            <Link to={pack.route}>Visitar</Link>
                        </div>
                        <img src={pack.image} alt={pack.alt} />
                    </article>
                ))}
            </section>
        </div>
    );
};

export default Packages;