import React from 'react';
import { Link } from 'react-router-dom';
import imgLuxuryMain from '../images/paquetelujoimg.jpg';
import imgLarcomar from '../images/larcomar.jpeg';
import imgLuxuryHotel from '../images/paquetehotellujo.jpg';
import '../css/tourism.css'
import '../css/page-content.css';

const TourismLuxury = () => {
    return (
        <div className="page-luxury-package">
            <div className="page-content">
                <article className="container1">
                    <div className="image-content">
                        <img src={imgLuxuryMain} alt="imagen de turismo de lujo" />
                    </div>
                    <div className="text">
                        <p>
                            Larcomar es un famoso centro comercial y destino turstico ubicado en el distrito de Miraflores,
                            Lima, Per. Construido sobre un
                            acantilado con vistas al ocano Pacfico, ofrece una experiencia nica con su combinacin de tiendas
                            de lujo, restaurantes de alta
                            cocina y entretenimiento. Es un lugar ideal para turistas, ya que cuenta con miradores panormicos,
                            bares y cafs con terrazas al aire
                            libre. Adems, su proximidad a hoteles de cinco estrellas y la Costa Verde lo convierte en un punto
                            clave para quienes visitan Lima.
                            Larcomar tambin alberga teatros, galeras de arte y opciones de vida nocturna. Su ubicacin
                            privilegiada y su oferta variada lo hacen
                            un atractivo imperdible en la ciudad.
                        </p>
                    </div>
                </article>

                <article className="container2">
                    <div className="data">
                        <div>
                            <h2><em>Datos del Paquete</em></h2>
                            <div className="line"></div>
                        </div>
                        <p><em>Reserva ahora tu lugar para una experiencia nica!</em></p>
                        <ul>
                            <li><i className='bx bxs-user'></i><em>Pasajeros mximo: 4</em></li>
                            <li><i className='bx bxs-calendar'></i><em>Horario de salida: 2:30 pm</em></li>
                            <li><i className='bx bxs-time'></i><em>Duracin: 4 das</em></li>
                            <li><i className='bx bxs-briefcase-alt-2'></i><em>Incluye: Transporte y alojamiento</em></li>
                            <li><i className='bx bxs-calendar-check'></i><em>Disponibilidad: Todos los das</em></li>
                            <li><i className='bx bxs-purchase-tag-alt'></i><em>Precio: S/ 695.00</em></li>
                        </ul>
                        <Link to="/reservation">
                            <button><em>Reservar</em></button>
                        </Link>
                    </div>

                    <figure>
                        <img src={imgLarcomar} alt="Imagen del Parque de las Aguas" />
                        <figcaption><em>Fotografa de Larcomar</em></figcaption>
                    </figure>
                </article>

                <article className="block">
                    <div className="photo">
                        <img src={imgLuxuryHotel} alt="imagen de turismo de lujo" />
                    </div>
                    <div className="text">
                        <h2><em>Alojamiento</em></h2>
                        <br />
                        <p>
                            El JW Marriott Hotel Lima es un lujoso hotel de cinco estrellas ubicado en el distrito de
                            Miraflores, frente al centro comercial Larcomar.
                            Ofrece impresionantes vistas al ocano Pacfico desde sus elegantes habitaciones y suites. Es
                            una opcin ideal para turistas y viajeros de negocios
                            gracias a su ubicacin estratgica y su servicio de alta calidad. Cuenta con restaurantes de
                            primera categora que sirven gastronoma peruana e internacional.
                            Adems, dispone de un moderno gimnasio, un spa y una piscina climatizada. Su centro de
                            convenciones y salones para eventos lo convierten en un lugar ideal para
                            reuniones y celebraciones. La atencin personalizada y el confort de sus instalaciones
                            garantizan una estada inolvidable. Su cercana a la Costa Verde permite a
                            los huspedes disfrutar de paseos y actividades al aire libre. Es uno de los hoteles ms
                            exclusivos de Lima y una opcin destacada para quienes buscan lujo y comodidad.
                        </p>
                    </div>
                </article>
            </div>
        </div>
    );
};

export default TourismLuxury;
