import React from 'react';
import imgAcercade1 from '../images/acercade1.jpg';
import imgAcercade2 from '../images/acercade2.jpg';
import '../css/about.css';

const About = () => {
    return (
        <div className='page-about'>
            <div className="about-intro">
                <h2><em>Somos Lima Tours</em></h2>
                <p>
                    Somos una empresa de turismo dedicada a crear experiencias inolvidables para nuestros clientes,
                    ofreciendo servicios de alta calidad y atencin personalizada. Nos especializamos en disear viajes
                    nicos que combinan aventura, cultura y confort. Contamos con un equipo de expertos apasionados por el
                    turismo, comprometidos con la excelencia y la sostenibilidad. Nuestra misin es conectar a los viajeros
                    con destinos extraordinarios, promoviendo el respeto por el medio ambiente y las comunidades locales.
                    Buscamos innovar constantemente para ofrecer experiencias seguras, autnticas y enriquecedoras.
                </p>
            </div>

            <div className="container">
                <article className="block">
                    <div className="image">
                        <img src={imgAcercade1} alt="Nuestra Misin" />
                    </div>
                    <div className="text">
                        <h2><em>Nuestra Misin</em></h2>
                        <p>
                            Nuestra misin es ofrecer experiencias autnticas y enriquecedoras que conecten a los
                            visitantes con la rica historia, cultura y diversidad de la ciudad. Nos dedicamos a proporcionar
                            servicios de alta calidad, brindando atencin personalizada a cada viajero para garantizar que
                            descubran lo mejor de Lima, desde su patrimonio colonial hasta su vibrante vida moderna. Promovemos
                            el turismo sostenible, respetuoso con el medio ambiente y las comunidades locales, fomentando el
                            cuidado de nuestros recursos naturales y el respeto por nuestras tradiciones. Nuestro objetivo es
                            crear viajes inolvidables que ofrezcan una inmersin total en la esencia de Lima.
                        </p>
                    </div>
                </article>

                <article className="bloque">
                    <div className="text">
                        <h2><em>Nuestra Visin</em></h2>
                        <p>
                            Nuestra visin es posicionar a la ciudad como un destino global de referencia, reconocido
                            por su diversidad cultural, su patrimonio histrico y su vibrante oferta gastronmica. Queremos
                            que Lima sea un lugar donde los viajeros encuentren experiencias nicas, combinando lo tradicional
                            con lo moderno, y puedan sumergirse en su rica herencia mientras disfrutan de la hospitalidad y el
                            confort. Buscamos promover un turismo responsable y sostenible, que impulse el desarrollo local,
                            proteja el medio ambiente y favorezca el bienestar de las comunidades. Nuestra visin es hacer de
                            Lima un destino turstico accesible, inclusivo y atractivo para todos.
                        </p>
                    </div>
                    <div className="image">
                        <img src={imgAcercade2} alt="Nuestra Visin" />
                    </div>
                </article>
            </div>
        </div>
    );
};

export default About;